* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.list {
  justify-content: center;
  list-style-type: none;
  display: flex;
}

.button-container {
  justify-content: center;
  padding-top: 150px;
  display: flex;
}

.button-mod:not(:last-child) {
  margin-right: 30px;
}

.link {
  text-decoration: none;
}

.list__item {
  padding-top: 50px;
}

.list__item:not(:last-child) {
  margin-right: 20px;
}

.btn {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background: linear-gradient(183deg, #06c 24%, #ff0 100%);
  border: 0 solid #06c;
  border-radius: 50px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 13px 30px;
  font-family: Roboto Condensed;
  font-size: 25px;
  font-weight: 900;
  transition: all 2.026s;
  display: flex;
  transform: translateY(0);
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.btn:hover {
  color: #ff0;
  background: linear-gradient(183deg, #06c 24%, #ff0 100%);
  border: 0 solid #06c;
  padding: 20px 50px;
  transition: all 2.026s;
  transform: translateY(0);
}

.back-btn {
  color: #fff;
  cursor: pointer;
  max-width: 120px;
  text-align: center;
  background-color: #06c;
  border: none;
  border-radius: 50px 0 50px 1px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px 15px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 100;
  transition: all 3s;
  display: flex;
  transform: translateY(0);
  box-shadow: 0 5px 15px rgba(0, 0, 0, .35);
}

.back-btn:hover {
  color: #264653;
  background-color: #fff;
  border: 2px solid #06c;
  padding: 10px 20px;
  transition: all 3s;
  transform: translateY(0);
}

